"use client";
import { useEffect, useState } from "react";
import {
  Chart as ChartJS,
  CategoryScale,
  LinearScale,
  PointElement,
  LineElement,
  Title,
  Tooltip,
  Legend,
  ChartOptions,
} from "chart.js";
import { Line } from "react-chartjs-2";
import { ChartBarComponent } from "@/components/bar-chart";

ChartJS.register(
  CategoryScale,
  LinearScale,
  PointElement,
  LineElement,
  Title,
  Tooltip,
  Legend
);

type Sale = {
  date: string;
  sales: number;
};

export default function Home() {
  const [data, setData] = useState<Sale[]>([]);
  const [total_Merchants, settotal_Merchants] = useState<number>(0);

  useEffect(() => {
    fetch("http://localhost:8000/sales")
      .then((res) => res.json())
      .then((json) => {
        setData(json.data);
        settotal_Merchants(json.total_Merchants);
      })
      .catch((err) => console.error("Error fetching sales data:", err));
  }, []);

  const chartData = {
    labels: data.map((item) => item.date),
    datasets: [
      {
        label: "Sales",
        data: data.map((item) => item.sales),
        borderColor: "rgba(54, 162, 235, 1)",
        backgroundColor: "rgba(54, 162, 235, 0.2)",
        fill: true,
        tension: 0.4,
        pointRadius: 5,
        pointHoverRadius: 7,
      },
    ],
  };

  const chartOptions: ChartOptions<"line"> = {
    responsive: true,
    plugins: {
      legend: {
        position: "top" as const,
      },
      title: {
        display: true,
        text: "ðŸ“ˆ Sales Over Time",
        font: {
          size: 18,
        },
      },
    },
    animation: {
      duration: 1000,
      easing: "easeInOutQuad",
    },
  };

  return (
    <main style={{ width: "600px", margin: "40px auto" }}>
      <ChartBarComponent />
    </main>
  );
}
